.block-donate {
  @include section-wrap(100%);
  margin-top: 100px;

  &__wrap,
  .wrap {
    width: 100%;
    @include flex(row, nowrap, space-between, flex-start);

    &__text,
    .text {
      width: 60%;
      max-width: 440px;
      &__head-donate {
        @include font(24px, 700, 29px, $text-main, left);
        margin-bottom: 24px;
      }
      &__desc-donate {
        @include font(16px, 400, 22px, $text-main, left);
      }
    }

    &__form,
    .form {
      width: 40%;
      @include flex(column, nowrap, center, center);
      border: 1px solid $bg-stats;
      border-radius: 4px;
      box-shadow: $shadow-input;
      padding: 30px 26px 40px;
      background: $text-light;

      &__box,
      .box {
        @include flex(column, nowrap, center, flex-start);
        max-width: 332px;
        &__input-wrap {
          @include flex(column, nowrap, center, flex-start);
        }
        &__item {
          @include font(14px, 700, 17px, $text-main, left);
          margin-top: 16px;
          margin-bottom: 4px;
        }
        &__input,
        .input {
          border: $border-light;
          border-radius: 4px;
          margin: 0;
          height: 40px;
          width: 100%;
          max-width: 330px;
          text-indent: 5px;
        }
        &__select,
        .select {
          display: flex;
          width: 100%;
          max-width: 332px;
          height: 42px;
          padding: 0;

          &__wrap-t,
          .wrap-t {
            border: $border-light;
            border-radius: 4px 0 0 4px;
            @include font(16px, 700, 19px, $text-main, center);
            padding: 8px 16px 10px;
            margin: 0;
          }
          &__input,
          .input {
            width: 100%;
            border: $border-light;
            border-radius: 0 4px 4px 0;
          }
        }

        &__accent-btn,
        .accent-btn {
          @include button(15px, 15px, $bg-green);
          margin-left: 38px;
          display: flex;
          height: 40px;
          max-width: 120px;
          margin-top: 26px;
          margin-left: 0;
          justify-content: center;

          &__icon-white-heart {
            margin-left: 9px;
          }
        }
        .accent-btn:hover {
          @include button-hover-active($btn-hover);
        }
        .accent-btn:active {
          @include button-hover-active($btn-active);
          transform: scale(1);
        }
      }
    }

    .input:valid .box-accent-btn {
      background: blue;
      border: 1px solid blue;
      @include button(15px, 15px, blue);
    }
  }
}

