      .text {
        width: 100%;
        max-width: 600px;
        &__head-donate {
          @include font(24px, 700, 29px, $text-main, left);
        }
      }

      &__form,
      .form {
        width: 100%;

        margin-left: 15px;

        &__box,
        .box {
          @include flex(column, nowrap, center, flex-start);

          &__item {
